.ProfileInfo {
	background: #252525;
	color: #fff; }
.bg_content {
	img {
		width: 100%;
		display: block;
		height: 300px;
		margin-bottom: 20px; } }
.about-profile__ProfileInfo {
	display: flex; }

.img__about-profile {
	width: 30%;
	margin-right: 20px;
	img {
		width: 100%;
		border-radius: 50%; } }
.name__about-profile, .contacts__about-profile {
	margin: 20px 30px 0 0;
	p {
		margin-bottom: 10px;
		font-size: 16px; }
	.orangeInfo {
		color: #FF7400;
		font-weight: 600;
		a {
			color: #FF7400;
			transition: 1s;
			padding: 3px;
			&:hover {
				background: #FF7400;
				color: #fff; } } } }
