.block__posts {
	color: #000;
	font-weight: 600; }

.posts {
	img {
		width: 50px;
		height: 50px;
		border-radius: 50%; } }

.form__text {
	div {
		&:first-child {
			textarea {
				width: 100%;
				height: 100px; } }
		&:last-child {
			text-align: right;
			button {
				background: #252525;
				border: none;
				color: #fff;
				padding: 5px 30px; } } } }
